.header {
  display: block;
  min-height: 56px;
  background-color: #BD5D38;
  margin: 0;
  padding: 0; }

.header__title {
  display: inline-block;
  margin: 20px 20px;
  font-family: 'Yanone Kaffeesatz';
  color: #f3f3f3; }

.container {
  box-sizing: border-box;
  padding: 0 20px;
  width: 320px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.toggle-button {
  width: 30px;
  height: 16px;
  padding: 0;
  margin: 20px;
  border: none;
  background-color: transparent;
  border-top: 2px solid #fff;
  border-bottom: 2px solid #fff;
  cursor: pointer; }

.toggle-button::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 30px;
  height: 2px;
  background-color: #fff; }

.menu {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: left; }

.menu__item {
  font-family: Arial, Helvetica, sans-serif;
  position: relative; }

.menu__link {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
  padding: 10px;
  font-family: "Fira Sans", Arial, sans-serif;
  font-size: 16px;
  font-weight: 700; }

.header__logo {
  display: none; }

.site-title {
  margin: 0;
  padding: 10px 0;
  font-family: "Yanone Kaffeesatz", Arial, sans-serif;
  color: #fff; }

.navigation {
  width: 100%;
  background-color: #BD5D38;
  border-top: 1px solid rgba(255, 255, 255, 0.2); }

.navigation__toggle {
  position: absolute;
  top: 18px;
  right: 20px; }

.navigation__authorization {
  margin: 0;
  padding: 0; }

.toggler {
  display: flex;
  flex-direction: column;
  width: 56px;
  height: 40px;
  padding: 7px 14px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  background-color: transparent; }

.toggler:hover, .toggler:focus {
  text-decoration: none; }

.toggler__icon {
  display: block;
  height: 2px;
  width: 100%;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.5); }

.category__list {
  list-style: none;
  padding: 0; }

.authorization-menu {
  list-style: none;
  padding: 0; }

.authorization-menu__item {
  font-family: Arial, Helvetica, sans-serif;
  position: relative; }

.authorization-menu__link {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
  padding: 10px;
  font-family: "Fira Sans", Arial, sans-serif;
  font-size: 16px;
  font-weight: 700; }

.carousel {
  position: relative;
  box-sizing: border-box;
  min-height: 150px; }

.carousel__control {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  text-align: center; }

.carousel__slides {
  display: none;
  width: 320px;
  height: 480px; }
  .carousel__slides:first-of-type {
    display: block;
    background: url("../../images/img-circuits-post-1.jpg") no-repeat center center;
    background-size: cover; }

.button__more {
  display: none; }

.button__about-us {
  text-decoration: none;
  color: #bd5d38;
  margin-right: 20px; }

.button__contacts {
  text-decoration: none;
  color: #bd5d38; }

.footer {
  vertical-align: middle;
  background-color: #06090F;
  color: #BD5D38; }

.footer__feedback-info {
  margin: 0;
  padding: 20px 0;
  text-align: center; }

.footer__social {
  margin: 0;
  padding: 20px;
  list-style: none; }

.footer__copyright {
  text-align: center;
  margin: 0;
  padding: 10px 0 10px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.3); }

.social-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }

.social-menu__link {
  display: block; }
  .social-menu__link--instagram {
    background: url("../../images/icon-instagram.svg") no-repeat center;
    width: 40px;
    height: 40px; }
  .social-menu__link--pinterest {
    background: url("../../images/icon-pinterest.svg") no-repeat center;
    width: 40px;
    height: 40px; }
  .social-menu__link--github {
    background: url("../../images/icon-github.svg") no-repeat center;
    width: 40px;
    height: 40px; }

